@import '../../styles/utils/vars';
@import '../../styles/utils/extends';
@import '../../styles/utils/mixins';

.checkout-button {
  @extend %buttonText;
  color: $colorWhite;
  box-shadow: none;
  width: 100%;
  height: 48px;

  @include onDesktop {
    width: 320px;
  }

  border: 1px solid transparent;
  background: $colorPrimary;

  &:hover {
    box-shadow: 0px 3px 13px rgba(23, 32, 49, 0.4);
  }
}
